/*----------  btn  ----------*/
.c-btn {
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  text-align: center;
  vertical-align: middle;
  user-select: none;
  display: inline-block;
  background-color: transparent;
  border: 0px;
  @include large-below {
    padding: 7px 36px;
  }
  @include large {
    padding: 16px 58px;
  }
  @each $key, $value in $colors {
    // * https://pjchender.github.io/2017/11/01/-sass-scss/
    $newValue: $value !default; 
    &--#{$key} {
      color: $white;
      background-color: $value;
    }
    &:hover, &:focus {
      // HSL-adjust-hue()https://changtsuiling.gitbooks.io/sass/chapter9/4-8-hslhan-6578-adjust-hue-han-shu.html
      background-color: darken(adjust-hue($newValue, -20deg), 10%);
    }
  };
}
/*----------  /btn  ----------*/