/*----------  typeseet  ----------*/
/* u-fontSize */
// fontsize scss 
@include fontSizeBatch ();
/*  */
.u-fontSize--3-5 { 
  /* 14px/4=3.5 */
  font-size: .875rem; 
}
/* / */
@include small {
  @include fontSizeBatch (sm);
  /*  */
  .u-fontSize-sm--3-5 { 
    /* 14px/4=3.5 */
    font-size: .875rem; 
  }
  /* / */
}
@include medium {
  @include fontSizeBatch (md);
  /*  */
  .u-fontSize-md--3-5 { 
    /* 14px/4=3.5 */
    font-size: .875rem; 
  }
  /* / */
}
@include large {
  @include fontSizeBatch (lg);
  /*  */
  .u-fontSize-lg--3-5 { 
    /* 14px/4=3.5 */
    font-size: .875rem; 
  }
  /* / */
}

/* u-fontSize */

/* u-lineHeight */
// line-height scss 
@include lineHeightBatch ();
@include small {
  @include lineHeightBatch (sm);
}
@include medium {
  @include lineHeightBatch (md);
}
@include large {
  @include lineHeightBatch (lg);
}
/* /u-lineHeight */

